import React from "react";
import { BrowserRouter, Routes, Route, Link } from "react-router-dom";
import Home from "./pages/Home";
import HomeTwo from "./pages/HomeTwo";
import HomeThree from "./pages/HomeThree";
import Profile from "./pages/Profile";
import NotFound from "./pages/NotFound";
import useFetch from "./useFetch";
import "./index.css";

const App = () => {
  const { data, loading, error } = useFetch(
    "https://akabab.github.io/superhero-api/api/all.json"
  );
  // if loading is true return loading symbol
  if (loading) {
    return <h1>Loading...</h1>;
  }
  if (error) console.log(error);
  return (
    <BrowserRouter>
      <h1 style={{ textAlign: "center" }}>WELCOME TO MY SUPERHERO GALLERY</h1>
      <Routes>
        <Route path="/" element={<Home data={data} />} />
        <Route path="/home2" element={<HomeTwo data={data} />} />
        <Route path="/home3" element={<HomeThree data={data} />} />
        <Route path={`/profile/:id`} element={<Profile />} />
        <Route path="*" element={<NotFound />} />
      </Routes>
      <div
        style={{
          textAlign: "center",
          margin: "20px",
          textDecorationLine: "none",
        }}
      >
        <Link to="/">PAGE 1</Link>
        <Link to="/home2">PAGE 2</Link>
        <Link to="/home3">PAGE 3</Link>
      </div>
    </BrowserRouter>
  );
};

export default App;
