import React from "react";
import { Link } from "react-router-dom";

const HomeTwo = ({ data }) => {
  const items = data.slice(21, 30);
  return (
    <div style={{ display: "flex" }}>
      {items.map((items, index) => {
        return (
          <div
            style={{
              justifyContent: "center",
              flexDirection: "row",
              alignItems: "center",
              display: "grid",
              marginLeft: "20px",
            }}
            key={index}
          >
            <Link to={`/profile/${items.id}`}>
              <h2>{items.name}</h2>
            </Link>
            <img src={items.images.md} />
            <br />
          </div>
        );
      })}
    </div>
  );
};

export default HomeTwo;
